<ul><template v-for="(item, index) in list"><li v-if="item.break" :class="&quot;break-&quot; + item.break"></li><li class="list-item"><span v-if="typeof item === &quot;string&quot;">{{ item }}</span><template v-else><span class="item-name" :class="item.professional ? &quot;professional&quot; : &quot;&quot;">{{ item.name }}
<span class="item-note" v-if="item.note">{{ item.note }}</span></span><recursive-list class="children-item" v-if="item.children" :list="item.children"></recursive-list></template></li></template></ul>