<div class="column-component"><template v-for="section in sections"><div v-if="section.break" :class="&quot;break-&quot; + section.break"></div><div class="section-name mb-2" v-if="section.name"><h2>{{ section.name }}</h2></div><div class="contacts-container" v-if="section.contacts"><div class="contact" v-for="contact in section.contacts"><a v-if="contact.link" :href="contact.link" :target="contact.link.startsWith(&quot;https&quot;) ? &quot;_blank&quot; : &quot;&quot;"><span class="badge has-link me-2 mb-2 align-middle"><svg-icon :icon="contact.icon"></svg-icon></span></a><template v-else><span class="badge me-2 mb-2 align-middle"><svg-icon :icon="contact.icon"></svg-icon></span></template><h3>{{ contact.value }}</h3></div></div><template v-else-if="section.languages"><ul class="text no-indent"><li v-for="language in section.languages"><strong class="language-name">{{ language.name }}: </strong><span>{{ language.level }}</span></li></ul></template><template v-else-if="section.list"><ListComponent class="text" :list="section.list" :class="section.class"></ListComponent></template><template v-else-if="section.items" v-for="item in section.items"><div v-if="item.break" :class="&quot;break-&quot; + item.break"></div><template v-if="item.degree"><div class="subsection-name mb-1"><h3><span class="position">{{ item.course }}</span><br>{{ item.place }}</h3></div><h3 class="time">{{ item.time }}</h3><h4>{{ item.degree }}:</h4><span class="text">{{ item.specialisation }}</span></template><template v-else-if="item.position"><div class="subsection-name mb-1"><h3><span class="position">{{ item.position }}</span><span>
â€¢ {{ item.place }}</span></h3></div><h3 class="time">{{ item.time }}</h3><h4 class="mt-2">Responsibilities:</h4><ListComponent class="text" :list="item.responsibilities"></ListComponent><div v-if="item.keyAccomplishments"><h4>Key accomplishments:</h4><ListComponent class="text" v-if="!!(item.keyAccomplishments instanceof Array)" :list="item.keyAccomplishments"></ListComponent><p class="text" v-else>{{ item.keyAccomplishments }}</p></div></template></template></template></div>